import { createStore } from 'vuex'

export default createStore({
  state: {

    arrayListadoBitacorasFull: [],
    arrayListadoBitacoras: [],
    arrayFiltros: [
      {
        creadoPor: [],
        status: [],
        destinos: [],
        tiposEquipos: [],
      }
    ],
    arrayBitacoras: [],
    arrayTiposEquipos: [],
    arraySecciones: [],
    arrayEquipos: [],
    arrayDestinos: [],
    arrayUsuariosPermitidos: [],
    arrayUnidades: [],
    arrayUsuariosTelegram: [],

  },

  mutations: {
    setListadoBitacoras(state, payload) {
      state.arrayListadoBitacoras = payload;
      state.arrayListadoBitacorasFull = payload;
    },

    setBitacoras(state, payload) {
      state.arrayBitacoras = payload;
    },

    setTiposEquipos(state, payload) {
      state.arrayTiposEquipos = payload;
    },

    setSecciones(state, payload) {
      state.arraySecciones = payload;
    },

    setEquipos(state, payload) {
      state.arrayEquipos = payload;
    },

    setDestinos(state, payload) {
      state.arrayDestinos = payload;
    },

    setUsuariosPermitidos(state, payload) {
      state.arrayUsuariosPermitidos = payload;
    },

    setUnidades(state, payload) {
      state.arrayUnidades = payload;
    },

    setUsuariosTelegram(state, payload) {
      state.arrayUsuariosTelegram = payload;
    },

  },

  actions: {

    // OBTIENE EL LISTADO DE LAS BITACORAS
    async getListadoBitacoras({ commit, state }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.listadoBitacoras) {
          commit("setListadoBitacoras", data.listadoBitacoras);

          state.arrayListadoBitacoras.forEach(element => {

            // STATUS
            if (!state.arrayFiltros[0].status.includes(element.status)) {
              state.arrayFiltros[0].status.push(element.status)
            }
            // CREADO POR
            if (!state.arrayFiltros[0].creadoPor.includes(element.creadoPor)) {
              state.arrayFiltros[0].creadoPor.push(element.creadoPor)
            }

            // DESTINO
            if (element.destinos) {
              element.destinos.forEach(item => {
                if (!state.arrayFiltros[0].destinos.includes(item.toUpperCase())) {
                  state.arrayFiltros[0].destinos.push(item.toUpperCase())
                }
              });
            }

            // TIPOS DE EQUIPOS
            if (element.tiposEquipos) {
              element.tiposEquipos.forEach(item => {
                if (!state.arrayFiltros[0].tiposEquipos.includes(item.toUpperCase())) {
                  state.arrayFiltros[0].tiposEquipos.push(item.toUpperCase())
                }
              });
            }

          });

        }

      } catch (error) {
        console.log(error);
      }
    },

    // OBTIENE LAS BITACORAS
    async getBitacoras({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();

        if (data.bitacoras) {
          commit("setBitacoras", data.bitacoras);
        }

        if (data.destinos) {
          commit("setDestinos", data.destinos);
        }

        if (data.tiposEquipos) {
          commit("setTiposEquipos", data.tiposEquipos);
        }

        if (data.seccionesSubsecciones) {
          commit("setSecciones", data.seccionesSubsecciones);
        }

        if (data.usuariosPermitidos) {
          commit("setUsuariosPermitidos", data.usuariosPermitidos);
        }

        if (data.unidades) {
          commit("setUnidades", data.unidades);
        }

        if (data.usuariosTelegram) {
          commit("setUsuariosTelegram", data.usuariosTelegram);
        }

      } catch (error) {
        console.log(error);
      }
    },

    // ACCIONES DE USUARIO
    async accionesUsuario({ commit }, array) {
      const Swal = require("sweetalert2");

      const Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 3000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      })

      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          Toast.fire({
            icon: data.response.toLowerCase(),
            title: data.message,
          })
          return true;
        }
        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },

    // ACCIONES DE USUARIO
    async getEquipos({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          commit("setEquipos", data.equipos);
          return await data.equipos;
        }
        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },

    // COMPRUEBA LA CONEXIÓN
    async getConexion({ commit }, array) {
      try {
        const res = await fetch(`../apis/bitacoras_parametros.php`, {
          method: "POST",
          body: JSON.stringify(array),
        });
        const data = await res.json();
        if (data.response == "SUCCESS") {
          return await data.conexion;
        }
        return false;
      } catch (error) {
        console.log(error);
        return false;
      }
    },





  },
  modules: {
  }
})
