import { createRouter, createWebHashHistory } from 'vue-router'
import Bitacora from '../views/Bitacora.vue'
import Bitacoraconfiguracion from '../views/Bitacoraconfiguracion.vue'

const routes = [
  {
    path: '/:idBitacora',
    name: 'Bitacora',
    component: Bitacora
  },

  {
    path: '/',
    name: 'Bitacoraconfiguracion',
    component: Bitacoraconfiguracion
  },

  // {
  // path: '/misbitacoras',
  // name: 'Misbitacoras',
  // route level code-splitting
  // this generates a separate chunk (incidencia.[hash].js) for this route
  // which is lazy-loaded when the route is visited.
  // component: () => import(/* webpackChunkName: "misbitacoras" */ '../views/Misbitacoras.vue')
  // },
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router